<template>
  <div id="CoursewareView">
    <head-top title="文件预览"></head-top>
    <r-file-preview :fileExt="fileExt" :fileName="name" :fileId="id"></r-file-preview>
  </div>
</template>

<script>
import RFilePreview from "@/components/RFilePreview";
export default {
  name: "",
  components: { RFilePreview },
  props: {},
  data() {
    return {
      id: this.$route.params.id,
      fileExt: this.$route.params.fileExt,
      name: this.$route.params.name,

      dataList: [],
      previewUrl: "",
      loading: false,
      diaologShow: false
    };
  }
};
</script>

<style lang='less' scoped>
#CoursewareView {
  width: 100%;
  height: 100%;
}
</style>


